import axios from 'axios';

const apiClient = axios.create({
  baseURL: 'http://157.230.27.124:3000', // Thay thế bằng URL của API thực tế
  headers: {
    'Content-Type': 'application/json',
  },
});
// Thêm Interceptor để kiểm tra và thêm mã xác thực
apiClient.interceptors.request.use(
    (config) => {
      const authToken = localStorage.getItem('authToken');
      if (authToken) {
        config.headers.Authorization = `Bearer ${authToken}`;
      }
      return config;
    },
    (error) => {
      return Promise.reject(error);
    }
  );
  
export default {
  // Ví dụ hàm để lấy danh sách các mục
  async createUser(data) {
  
      const response = await apiClient.post('/client/users',data); // Đường dẫn API thực tế
      return response.data;
    
  },

  // Ví dụ hàm để tạo một mục mới
  async getprofile() {
   
      const response = await apiClient.get('/client/users?mod=my_profile');
      return response.data;
   
  },
  async uploadFile(formData) {

      const response = await apiClient.post('/api/upload', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      return response;
 
  },

  async updateProfile(data) {
   
    const response = await apiClient.post('/client/users',data);
    return response.data;
 
},

async vay(data) {
   
  const response = await apiClient.post('/client/users',data);
  return response.data;

},
  
  // Các hàm khác tương tự cho các yêu cầu API khác
};
