import { createRouter, createWebHistory } from 'vue-router';

// Import your components for the routes
import Home from './views/Home.vue';
import Me from './views/Me.vue'
import wallet from "./views/wallet.vue";
import history from "./views/history.vue";
import MyContract from './views/vay/my-contract.vue';
import DetailProfile from './views/profile/detail-profile.vue';
import Auth from './views/auth/auth.vue';
import Signup from './views/auth/signup.vue';
import Vay from './views/vay/vay.vue';
import contracts from './views/Contracts.vue'
import Verify from './views/verify/verify.vue';
import Verify2 from './views/verify/verify2.vue';
import Verify3 from './views/verify/verify3.vue';
import api from './api/api';
import Verify4 from './views/verify/verify4.vue';
import NotFound from './views/NotFound.vue'
const routes = [
  {
    path: '/', component: Home,
    meta: { requiresAuth: true },
    name: 'Home',
  },
  { path: '/wallet', component: wallet },
  { path: '/history', component: history },
  { path: '/me', component: Me },
  { path: '/vay', component: Vay },
  { path: '/auth', component: Auth, name: 'Login' },
  { path: '/signup', component: Signup },
  { path: '/my-contract', component: MyContract },
  { path: '/detail-profile', component: DetailProfile },
  { path: '/contracts', component: contracts },
  { path: '/verify', component: Verify },
  { path: '/verify-step-2', component: Verify2 },
  { path: '/verify-step-3', component: Verify3 },
  { path: '/verify-step-4', component: Verify4 },
  {
    path: '/:catchAll(.*)',
    component: NotFound, // Use the NotFound component for unmatched paths
  },
  // Add more routes as needed
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

router.beforeEach(async(to, from, next) => {
  // const requiresAuth = to.matched.some(record => record.meta.requiresAuth);
  // const isLoggedIn = /* Kiểm tra trạng thái xác thực của người dùng */;

  //  window.location.href ="http://localhost:8080/auth"
  //  next()
let user = await api.getprofile()
   
  if (to.path == '/auth' || to.path == '/signup') {
    console.log('trang dang nhap')
    next()
  } else {
    // window.location.href ="http://localhost:8080/auth"
    if(user.status){
      window.user =user.data[0]
      next()
    }else{
      next({ name: 'Login' })
    }
    console.log(from)
   
  }
});

export default router;