
<template>
    <div id="app">
        <div style="padding-bottom: 80px;">
            <div class="fadein">
                <div class="container" style="padding: 0px !important;">
                    <div class="head"></div>
                    <div class="vay-header" style="background: rgb(216, 25, 33);">
                        <div>
                            <RouterLink to="/me"> <span role="img" aria-label="left" class="anticon anticon-left arrow-icon"
                                    style="color: rgb(255, 255, 255);"><svg viewBox="64 64 896 896" focusable="false"
                                        data-icon="left" width="1em" height="1em" fill="currentColor" aria-hidden="true">
                                        <path
                                            d="M724 218.3V141c0-6.7-7.7-10.4-12.9-6.3L260.3 486.8a31.86 31.86 0 000 50.3l450.8 352.1c5.3 4.1 12.9.4 12.9-6.3v-77.3c0-4.9-2.3-9.6-6.1-12.6l-360-281 360-281.1c3.8-3 6.1-7.7 6.1-12.6z">
                                        </path>
                                    </svg></span></RouterLink>
                        </div> <span class="ant-typography header-text" style="color: rgb(255, 255, 255);">Chọn khoản
                            vay</span>
                        <div></div>
                    </div>
                    <div class="vay-body">
                        <div class="title-container"><span class="ant-typography title"
                                style="color: rgb(255, 255, 255);">Số tiền vay</span></div>
                        <div class="input-container"><input v-model="data.khoanvay" placeholder="Nhập số tiền cần vay"
                                min="20000000" max="500000000" type="number" inputmode="decimal"
                                class="ant-input input-field" style="min-width: 100%;"></div>
                        <div class="subtitle"><span class="ant-typography">Từ 20.000.000đ</span><span
                                class="ant-typography">Đến 500.000.000đ</span></div>
                        <div class="month-container" style="padding: 10px;"><span class="ant-typography">Chọn thời hạn
                                vay</span>

                            <div tabindex="0" class="ant-select ant-select-enabled"
                                style="border-radius: 100px; min-width: 150px;">
                                <select style="padding: 5px;" v-model="data.thoihan">
                                    <option disabled value="">Chọn thời hạn
                                        vay</option>
                                    <option value="6">6 tháng </option>
                                    <option value="12">12 tháng</option>
                                    <option value="24">24 tháng</option>
                                    <option value="36">36 tháng </option>
                                    <option value="48"> 48 tháng</option>
                                    <option value="60">60 tháng</option>
                                </select>
                            </div>
                        </div>
                    </div>
                    <div class="form-container">
                        <div class="information-form"><span class="ant-typography form-title"
                                style="color: rgb(255, 255, 255); font-weight: bold;">Thông tin khoản vay</span>
                            <div class="information">
                                <div class="details-information"><span class="ant-typography">Số tiền</span><span
                                        class="ant-typography"> đ</span></div>
                                <div class="details-information"><span class="ant-typography">Thời hạn vay</span><span
                                        class="ant-typography"> tháng</span></div>
                                <div class="details-information"><span class="ant-typography">Ngày vay</span><span
                                        class="ant-typography">5/8/2023</span></div>
                                <div class="details-information"><span class="ant-typography">Hình thức thanh
                                        toán</span><span class="ant-typography">Trả góp mỗi tháng</span></div>
                            </div>
                        </div>
                    </div>
                    <div>
                        <div class="old-debt-text"><span class="ant-typography"
                                style="flex: 2 1 0%; color: rgb(102, 102, 102); font-size: 14px;">Trả nợ kì
                                đầu</span><span class="ant-typography"
                                style="flex: 2 1 0%; color: rgb(62, 62, 62); font-size: 16px;">0
                                VND</span></div>
                        <div class="old-debt-text"><span class="ant-typography"
                                style="flex: 2 1 0%; color: rgb(102, 102, 102); font-size: 14px;">Lãi suất hàng
                                tháng</span><span class="ant-typography"
                                style="flex: 2 1 0%; color: rgb(62, 62, 62); font-size: 16px;">1%</span></div>
                        <div class="old-debt-text"><a class="ant-typography">Chi tiết trả nợ</a></div>
                    </div>
                    <div class="btn-container"><button type="button" v-on:click="vay()"
                            class="ant-btn ant-btn-default confirm-btn"><span class="ant-typography btn-title">Xác nhận
                                khoản vay</span></button></div>
                </div> <!---->
            </div> <!----> <!---->
        </div>
    </div>
</template>
  
<script>
import api from '../../api/api';
import router from '@/router';
import Swal from 'sweetalert2';
export default {
    name: 'MeProfile',
    data() {
        return {
            data: {
                khoanvay: "",
                thoihan: "",
            },
            inputValue:"",
            selected: "",
            message: 'Đây là nội dung của Me!'
        };
    },
    methods: {
        vay: async function () {
            
            if (!this.data.khoanvay || !this.data.thoihan) {
                return Swal.fire({
                    title: 'Thông báo',
                    text: 'Không được để thông tin trống',
                    icon: 'info',
                    timer: 2000, // Thời gian tính bằng mili giây (3 giây ở đây)
                    showCancelButton: false,
                    showConfirmButton: false
                });
            } else {
                let data = {
                    khoanvay: this.data.khoanvay,
                    thoihan: this.data.thoihan,
                    mod: "vay"
                }
                let vay = await api.vay(data)
                if (vay.status) {
                    Swal.fire({
                        title: 'Thông báo',
                        text: 'Hồ sơ đã hoàn thành',
                        icon: 'success',
                        timer: 2000, // Thời gian tính bằng mili giây (3 giây ở đây)
                        showCancelButton: false,
                        showConfirmButton: false
                    });
                    setTimeout(() => {
                        router.push('/me');
                    }, 2000);

                }
            }
        }
    },
   
    
}
</script>
  
  